<template>
  <div class="modal d-block" tabindex="-1">
    <div class="modal-dialog modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h6 class="modal-title"> </h6>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="ordersWidget.modal = false"></button>
        </div>
        <div class="modal-body">
          <div class="orders-table table-responsive m-0">
            <table class="table-bordered border-primary text-center w-100" height="100">
              <thead>
                <tr>
                  <th class="text-center">
                     
                  </th>
                  <th class="text-center">
                    
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in orders"
                  :key="item.id"
                >
                  <td :title="item.filename" class="p-3"><img class="mx-1" :src="xlsx" width="50" /> {{item.filename.length > 20 ? item.filename.substr(0, 17) + '...' : item.filename}}</td>
                  <td title=" " class="p-3"><a class="text-decoration-none" :href="`${url}/${item.href}`" target="_blank">
                    <svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" width="50" height="50" viewBox="0 0 24 24">
                        <path d="M19.355,10.036C18.674,6.595,15.641,4,12,4C9.108,4,6.603,5.639,5.352,8.036C2.343,8.36,0,10.906,0,14c0,3.314,2.686,6,6,6 h13c2.761,0,5-2.239,5-5C24,12.36,21.948,10.221,19.355,10.036z M12,18l-5-5h3V9h4v4h3L12,18z"></path>
                    </svg>
                  </a></td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="ordersWidget.modal = false"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import toastr from 'toastr';
import 'toastr/toastr.scss';
import xlsx from '@/assets/xlsx.png';
import showOrdersFunc from '@/mixins/api.js';

export default {
    name: "OrdersModal",
    mixins: [showOrdersFunc],
    props: {
      ordersWidget: Object
    },
    created() {
      toastr.options = {
        closeButton: true,
        progressBar: true,
        positionClass: 'toast-top-center',
        timeOut: 1300,
        toastClass: 'custom-toast',
      }

      this.showOrders();
    },
    data() {
      return {
        url: this.$store.getters.getUrl,
        orders: null,
        xlsx: xlsx
      }
    },
    methods: {
      blockAllButtons() {
        const news = [...document.querySelectorAll('.news')];
        const contacts = [...document.querySelectorAll('.contacts')];
        const authModalButton = [...document.querySelectorAll('.authModalButton')];


        const disFunc = (block) => {
          block.forEach(e => e.disabled = true)
        }

        disFunc(news); disFunc(contacts); disFunc(authModalButton); 

        document.querySelectorAll('.searcherSubmit').disabled = true
        document.querySelector('.news-footer').disabled = true
        document.querySelector('.contacts-footer').disabled = true
        document.querySelector('.searcherSubmit').disabled = true
        document.querySelector('.album').style.pointerEvents = 'none';

        document.querySelector('.cs-loader').classList.remove('d-none');
      },

      unBlockAllButtons() {
        const news = [...document.querySelectorAll('.news')];
        const contacts = [...document.querySelectorAll('.contacts')];
        const authModalButton = [...document.querySelectorAll('.authModalButton')];


        const disFunc = (block) => {
          block.forEach(e => e.disabled = false)
        }

        disFunc(news); disFunc(contacts); disFunc(authModalButton); 


        document.querySelectorAll('.searcherSubmit').disabled = false
        document.querySelector('.news-footer').disabled = false
        document.querySelector('.contacts-footer').disabled = false
        document.querySelector('.searcherSubmit').disabled = false
        document.querySelector('.album').style.pointerEvents = 'auto';

        document.querySelector('.cs-loader').classList.add('d-none');
      },

      async showOrders() {
        this.blockAllButtons();

        var orders = sessionStorage.getItem('orders');
        if(orders !== null) {
          orders = JSON.parse(orders);
          await this.unBlockAllButtons();
          return this.orders = orders;
        }

        try {
          const request = await this.showOrdersFunc();
          const status = request.status;
          const message = request.message;

          if(status === 200) {
            sessionStorage.setItem('orders', JSON.stringify(message));
            this.unBlockAllButtons();
            return this.orders = message;
          }

          toastr.error(message);
        } catch (error) {
          console.error(error);
          toastr.error(error.message);
        }

        return this.unBlockAllButtons();
      }
    }
}
</script>